import React from 'react'
import PropTypes from 'prop-types'
import Navbar from './Navbar'
// import { Link } from 'react-router-dom';

export default function TextForm(proos) {
  return (
    <>
        <nav className={`navbar navbar-expand-lg navbar-${proos.mode} bg-${proos.mode}`}>
        <div className="container-fluid">
        {/* <Link className="navbar-brand" to="/">{proos.title}</Link> */}
        <a className="navbar-brand" href="/">{proos.title}</a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
                <a className="nav-link active" aria-current="page" href="/">Home</a>
                {/* <Link className="nav-link active" aria-current="page" to="/">Home</Link> */}
            </li>
            {/* <li className="nav-item">
                <Link className="nav-link" to="/about">ABOUT</Link>
            </li> */}
            </ul>
            <div className={`form-check form-switch mx-1 text-${proos.mode === 'light'?'dark':'light'}`}>
            <input className="form-check-input" type="checkbox" onClick={proos.tooglemode} role="switch" id="flexSwitchCheckDefault"/>
            <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Enable Dark Mode</label>
            </div>
            {/* <form className="d-flex" role="search">
            <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
            <button className="btn btn-success" type="submit">Search</button>
            </form> */}
        </div>
        </div>
    </nav>
  </>
  )
}

Navbar.prototype = {title: PropTypes.string.isRequired};
Navbar.defaultProps= {title: 'Set Title Here'};